.slide-top {
  animation: slide-top 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.slide-in-bck-center {
  animation: slide-in-bck-center 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.tracking-in-expand {
  animation: tracking-in-expand 1s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}

.appear {
  animation: opacity 1s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
.scaleUp {
  animation: scaling 1s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
.bgScaleUp {
  animation: bg-scaling 1s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}

@keyframes slide-top {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100px);
  }
}

@keyframes slide-bottom {
  0% {
    top: -20%;
    opacity: 0;
  }
  100% {
    top: 60%;
    opacity: 1;
  }
}

@keyframes slide-in-bck-center {
  0% {
    transform: translateZ(600px);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes pulsate-bck {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes opacity {
  0% {
    transform: scale(0.6);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes scaling {
  0% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1);
  }
}


@keyframes bg-scaling {
  0% {
    background-size: 200px;
  }
  100% {
    background-size: 782px;
    @media screen and (min-width: $tab) {
      background-size: 991px;
      background-position: 50% 55px;
    }
    @media screen and (min-width: $dsk) {
      background-size: 782px;
    }
  }
}
