.footer {
  align-items: flex-start;
  gap: 32px;
  font-family: Jomolhari;
  line-height: 22.4px;
  letter-spacing: 0.16px;
  font-weight: 400;
  font-size: 16px;
}

.footer-logo {
  width: 127px;
  height: auto;
}
.footer-nav {
  cursor: pointer;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8;
}
.footer-contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8;
}
.footer-socials {
  display: flex;
  gap: 15px;
  margin-bottom: 64px;
  align-items: center;
  justify-content: center;
}

.footer-right-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: flex-start;
}

.footer-socials ~ img {
  width: 24px;
  height: 24px;
}
.footer-rights {
  font-size: 12px;
  line-height: 16.8px;
  letter-spacing: 0.12px;
  text-align: center;

  margin-bottom: 16px;
}

@media screen and (min-width: $tab) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
  }
  .footer-logo {
    width: 169px;
  }
  .footer-right-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: flex-end;
  }
  .footer-contacts {
    align-items: flex-end;
  }
}
@media screen and (min-width: $dsk) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    font-size: 18px;

    line-height: 25.2px;
    letter-spacing: 0.18px;
  }
  .footer-logo {
    width: 250px;
  }
  .footer-nav {
    gap: 16px;
  }
  .footer-contacts {
    gap: 16px;
  }
  .footer-socials {
    margin-bottom: 85px;
  }
}
